import * as React from 'react';
import './AppSider.css';
import {
    Alignment, Button, Card, Menu, Navbar, NavbarGroup, NavbarHeading, Position,
    Tooltip
} from '@blueprintjs/core';
import { push } from 'react-router-redux';
import { store } from '../../store';
import NavItem from './NavItem';
import { SiderState } from './reducer';
import { connect } from 'react-redux';
import { RootState } from '../../root-reducer';
import { LoginState } from '../pages/LoginPage/reducer';

interface ComponentProps {
    state: SiderState;
    login: LoginState;
}

class AppSider extends React.Component<ComponentProps> {
    constructor(props: ComponentProps) {
        super(props);
    }

    render() {
        if (this.props.state.showSider) {
            return (
                <div className="AppSider">
                    <Navbar id="UserLogin">
                        <NavbarGroup>
                            <NavbarHeading className="pt-navbar-heading">
                                {this.props.login.loginInfo.user.username}
                            </NavbarHeading>
                        </NavbarGroup>
                        <NavbarGroup align={Alignment.RIGHT}>
                            <Tooltip content="Выход" position={Position.RIGHT}>
                                <Button className="pt-minimal" icon="log-out"/>
                            </Tooltip>
                        </NavbarGroup>
                    </Navbar>
                    <Card id="Navigator">
                        <Menu>
                            <NavItem text={'Логин'} link={'/login'} handlerClick={this.navClick}/>
                            <NavItem text={'Пользователи'} link={'/users'} handlerClick={this.navClick}/>
                            <NavItem text={'Структура'} link={'/structure'} handlerClick={this.navClick}/>
                            <NavItem text={'Должности'} link={'/positions'} handlerClick={this.navClick}/>
                            <NavItem text={'Сессии'} link={'/sessions'} handlerClick={this.navClick}/>
                        </Menu>
                    </Card>
                </div>
            );
        } else {
            return(<div/>);
        }
    }

    private navClick = (link: string): void => {
        store.dispatch(push(link));
    }
}

const mapStateToProps = (state: RootState) => {
    return {
        state: state.sider,
        login: state.login
    };
};

export default connect(mapStateToProps)(AppSider);