import * as React from 'react';
import './Main.css';
import { Route, Switch } from 'react-router';
import LoginPage from '../pages/LoginPage/LoginPage';
import UsersPage from '../pages/UsersPage/UsersPage';
import StructurePage from '../pages/StructurePage/StructurePage';
import PositionsPage from '../pages/PositionsPage/PositionsPage';
import SessionsPage from '../pages/SessionsPage/SessionsPage';

export default class Main extends React.Component {
    render () {
        return (
            <main className="Main">
                <Switch>
                    <Route path="/login" component={LoginPage} onChange={() => {console.log(); }}/>
                    <Route path="/users" component={UsersPage} />
                    <Route path="/structure" component={StructurePage} />
                    <Route path="/positions" component={PositionsPage}/>
                    <Route path="/sessions" component={SessionsPage} />
                    <Route component={LoginPage}/>
                </Switch>
            </main>
        );
    }
}