import * as React from 'react';
import './Loading.css';
import { Card, Spinner } from '@blueprintjs/core';

const Loading: React.SFC<{message: string}> = (props) => {
    return (
        <div className="LoadingContainer">
            <Card className="LoadingCard">
                <h5>{props.message}</h5>
                <Spinner/>
            </Card>
        </div>
    );
};

export default Loading;