import * as React from 'react';
import './Error.css';
import { Button, Card } from '@blueprintjs/core';

const Error: React.SFC<{message: string}> = (props) => {
    return (
        <div className="ErrorContainer">
            <Card>
                <h5>{props.message}</h5>
                <Button icon="refresh"/>
            </Card>
        </div>
    );
};

export default Error;