import * as React from 'react';
import { Button, Card } from '@blueprintjs/core';
import * as SessionsByDateGql from '../../../graphql/Session/SessionsByDate.graphql';
import './SessionPage.css';
import { QueryResult } from 'react-apollo';
import { sessionByDateFragment, sessionFragment, SessionsByDateQuery } from '../../../queries-types';
import Loading from '../../Loading/Loading';
import Error from '../../Loading/Error';
import Query from 'react-apollo/Query';

interface SessionsPageProps {

}

interface SessionsPageState {

}

class SessionsByDateComp extends Query<SessionsByDateQuery> {
}

interface SessionItemProps {
    session: sessionFragment;
}

const SessionItem: React.SFC<SessionItemProps> = props => {
    return (
        <Card className="SessionItem">
            <h6>{props.session.time}</h6>
            <div>{props.session.user.username}</div>
            <Button className="SessionRemoveButton" icon="cross" minimal={true}/>
        </Card>
    );
};

export default class SessionsPage extends React.Component<SessionsPageProps, SessionsPageState> {
    render() {
        return (
            <SessionsByDateComp
                query={SessionsByDateGql}
                displayName="SessionsPage"
            >
                {
                    (query: QueryResult<SessionsByDateQuery>) => {
                        if (query.loading) {
                            return <Loading message="Загрузка данных"/>;
                        }
                        if (query.error) {
                            return <Error message="Ошибка загрузки данных"/>;
                        }
                        return (
                            <div className="Page Padding">
                                {
                                    query.data.sessionsByDate.map((item: sessionByDateFragment) => {
                                        return (
                                            <div key={item.date}>
                                                <h5>{item.date}</h5>
                                                {
                                                    item.sessions.map((session: sessionFragment) => {
                                                        return (
                                                            <SessionItem key={session.id} session={session}/>
                                                        );
                                                    })
                                                }
                                            </div>
                                        );
                                    })
                                }
                            </div>
                        );
                    }
                }
            </SessionsByDateComp>
        );
    }
}