import * as React from 'react';
import './UsersPage.css';
import {
    UsersQuery, userFragment
} from '../../../queries-types';
import Query from 'react-apollo/Query';
import * as GetAllUsersQueryGql from '../../../graphql/UsersQuery.graphql';
import { QueryResult } from 'react-apollo';
import Loading from '../../Loading/Loading';
import UserItem from './UserItem';
import {
    Button, ButtonGroup, Label, Popover,
    Switch, Position, Radio, Card, RadioGroup
} from '@blueprintjs/core';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';
import { headerActions } from '../../AppHeader/actions';
import UserDialog from './UserDialog';
import RolesSelect from './RolesSelect';
import PositionSelect from './PositionSelect';
import Error from '../../Loading/Error';

interface UsersPageProps {
    setMenu: typeof headerActions.setMenu;
}

interface UserPageState {
    isDialogOpen: boolean;
    user: userFragment;
}

class UsersQueryComp extends Query<UsersQuery> {}

class UsersPage extends React.Component<UsersPageProps, UserPageState> {
    private menu = (
        <ButtonGroup>
                <Button
                    onClick={() => {this.createUser(); }}
                >
                    Создать
                </Button>
            <Popover
                position={Position.BOTTOM_LEFT}
                content={
                    <Card>
                        <Switch label="Обратный порядок"/>
                        <RadioGroup
                            onChange={() => {console.log('hello'); }}
                            label="Поле"
                        >
                            <Radio label="Название аккаунта" value="username"/>
                            <Radio label="Фамилия" value="lastname"/>
                            <Radio label="Имя" value="firstname"/>
                            <Radio label="Отчество" value="middlename"/>
                            <Radio label="Должность" value="position"/>
                        </RadioGroup>
                    </Card>
                }
            >
                <Button icon="sort" rightIcon="caret-down">Сортировка</Button>
            </Popover>
            <Popover
                position={Position.BOTTOM_LEFT}
                content={
                    <Card>
                        <Label text="Название аккаунта">
                            <input className="pt-input"/>
                        </Label>
                        <Label text="Фамилия">
                            <input className="pt-input"/>
                        </Label>
                        <Label text="Имя">
                            <input className="pt-input"/>
                        </Label>
                        <Label text="Отчество">
                            <input className="pt-input"/>
                        </Label>
                        <PositionSelect/>
                        <RolesSelect/>
                    </Card>
                }
            >
                <Button icon="filter" rightIcon="caret-down">Фильтры</Button>
            </Popover>
        </ButtonGroup>
    );

    constructor (props: UsersPageProps) {
        super(props);
        props.setMenu(this.menu);
        this.state = {
            isDialogOpen: false,
            user: null
        };
    }

    render() {
        return (
            <UsersQueryComp query={GetAllUsersQueryGql} displayName="UsersPage">
            {
                (x: QueryResult<UsersQuery>) => {
                    if (x.loading) {
                        return <Loading message="Загрузка данных"/>;
                    }
                    if (x.error) {
                        return <Error message="Ошибка загрузки данных"/>;
                    }
                    return (
                        <div className="UsersList">
                            {
                                x.data.users.map((item: userFragment) => {
                                    return <UserItem key={item.id} user={item} onClick={this.userClick}/>;
                                })
                            }
                            {
                                this.state.user &&
                                <UserDialog
                                    isDialogOpen={this.state.isDialogOpen}
                                    userData={this.state.user}
                                    onClose={this.onDialogClose}
                                />
                            }
                        </div>
                    );
                }
            }
            </UsersQueryComp>
        );
    }

    private userClick = (userData: userFragment) => {
        this.setState({
            isDialogOpen: true,
            user: userData
        });
    }

    private onDialogClose = () => {
        this.setState({isDialogOpen: false});
    }

    private createUser = () => {
        this.setState({
            isDialogOpen: true,
            user: {
                id: '',
                username: '',
                firstname: '',
                lastname: '',
                middlename: '',
                email: '',
                enabled: false,
                position: null,
                roles: []
            }
        });
    }
}

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators(
    {
        setMenu: headerActions.setMenu
    },
    dispatch);

export default connect(null, mapDispatchToProps)(UsersPage);