import * as React from 'react';
import './PositionsPage.css';
import * as PositionsQueryGql from '../../../graphql/PositionsQuery.graphql';
import Query from 'react-apollo/Query';
import {
    positionFragment, PositionInput, PositionsQuery
} from '../../../queries-types';
import { QueryResult } from 'react-apollo';
import Loading from '../../Loading/Loading';
import Error from '../../Loading/Error';
import {
    Button, ButtonGroup, Card, Label, Popover, Position
} from '@blueprintjs/core';
import PositionItem from './PositionItem';
import { headerActions } from '../../AppHeader/actions';
import { bindActionCreators, Dispatch } from 'redux';
import { connect } from 'react-redux';
import PositionDialog from './PositionDialog';

interface PositionsPageProps {
    setMenu: typeof headerActions.setMenu;
}

interface PositionsPageState {
    isDialogOpen: boolean;
    position: positionFragment;
}

class PositionsQueryComp extends Query<PositionsQuery> {}

class PositionsPage extends React.Component<PositionsPageProps, PositionsPageState> {
    private menu = (
        <ButtonGroup>
            <Button icon="add" onClick={() => {this.createPosition(); }}>Создать</Button>
            <Popover
                position={Position.BOTTOM_LEFT}
                content={
                    <Card>
                        <Label text="Название">
                            <input className="pt-input"/>
                        </Label>
                    </Card>
                }
            >
                <Button icon="filter" rightIcon="caret-down">Фильтры</Button>
            </Popover>
        </ButtonGroup>
    );

    constructor (props: PositionsPageProps) {
        super(props);
        props.setMenu(this.menu);
        this.state = {
            isDialogOpen: false,
            position: {
                id: '',
                name: ''
            }
        };
    }

    render () {
        return (
            <PositionsQueryComp query={PositionsQueryGql} displayName="PositionsPage">
            {
                (positionsResult: QueryResult<PositionsQuery>) => {
                    if (positionsResult.loading) {
                        return <Loading message="Загрузка данных"/>;
                    }
                    if (positionsResult.error) {
                        return <Error message="Не удалось загрузить данные"/>;
                    }
                    return (
                        <div id="StructureList">
                            {
                                positionsResult.data.positions.map((item: positionFragment) => {
                                    return <PositionItem
                                        key={item.id}
                                        position={item}
                                        onClick={this.positionClick}
                                    />;
                                })

                            }
                            {
                                <PositionDialog
                                    position={this.state.position}
                                    isDialogOpen={this.state.isDialogOpen}
                                    onClose={this.formClose}
                                />
                            }
                        </div>
                    );
                }
            }
            </PositionsQueryComp>
        );
    }

    private positionClick = (positionData: PositionInput) => {
        this.setState({
            isDialogOpen: true,
            position: {
                id: positionData.id,
                name: positionData.name
            }
        });
    }

    private formClose = () => this.setState({
        isDialogOpen: false
    })

    private createPosition = () => {
        this.setState({
            isDialogOpen: true,
            position: {
                id: '0',
                name: ''
            }
        });
    }

}

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators(
    {
        setMenu: headerActions.setMenu
    },
    dispatch);

export default connect(null, mapDispatchToProps)(PositionsPage);